import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import { Empty, Collapse, Select, DatePicker, Row, Col, Divider } from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/yearlySalesReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;

const redirectTo = "/dashboard/report-menus";

class YearlySalesReport extends Component {
  state = {
    filter: {
      id_location: null,
      id_customer: null,
      year: moment().startOf("month")
    },
    open: false
  };

  // Get All data diawal
  componentDidMount() {
    const { getYearlySalesReports } = this.props;
    getYearlySalesReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.yearlySalesReport.error) {
      notification("error", this.props.yearlySalesReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.yearlySalesReport.success) {
      notification("success", this.props.yearlySalesReport.success);
      this.props.clearSuccess();
    }
  }

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "year" && value) {
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          year: value
        }
      });
    } else if (name === "year" && !value) {
      this.setState({
        filter: {
          ...this.state.filter,
          year: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.year) {
      string
        ? (string += "&year=" + filter.year.format("YYYY"))
        : (string += "year=" + filter.year.format("YYYY"));
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }
    if (filter.id_customer) {
      string
        ? (string += "&id_customer=" + filter.id_customer)
        : (string += "id_customer=" + filter.id_customer);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getYearlySalesReports } = this.props;
    //const { current_page } = this.props.yearlySalesReport;
    let filter = this.filterValue();

    getYearlySalesReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getYearlySalesReports } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_customer: null,
        year: moment().startOf("month")
      }
    });
    getYearlySalesReports();
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "month" }),
        dataIndex: "month",
        rowKey: "month",
        width: "20%",
        render: text => <IntlMessages id={text.toLowerCase()}></IntlMessages>
      },
      {
        title: getLanguage({ id: "sales.totalCash" }),
        dataIndex: "total_cash",
        rowKey: "total_cash",
        width: "40%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "sales.totalCredit" }),
        dataIndex: "total_credit",
        rowKey: "total_credit",
        width: "40%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      }
    ];

    // Data setup
    const {
      yearly_sales_reports,
      locations,
      customers,
      filter,
      filter_select
    } = this.props.yearlySalesReport;
    //console.log(this.props.yearlySalesReport.base_currency); //console.log(this.state);
    const dataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    yearly_sales_reports.map((data, key) => {
      dataSource.push({
        ...data,
        key
      });
    });

    const { loading, loadingForeign } = this.props.yearlySalesReport;

    const fileName = getLanguage({ id: "yearlySalesReports.pageHeader" })
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="yearlySalesReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="yearlySalesReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
              filterDate="year"
              dateFormat="YYYY"
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => document.title = siteConfig.documentTitle}
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              loading={loading}
              locations={locations}
              title="yearlySalesReports.pageHeader"
            />

            <ExcelWrapper
              title="yearlySalesReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
              filterDate="year"
              dateFormat="YYYY"
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="sidebar.customer" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_customer"
                      )}
                      value={
                        this.state.filter.id_customer
                          ? this.state.filter.id_customer
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={<IntlMessages id="select.customer" />}
                      optionFilterProp="children"
                      filterOption={true}
                      // loading={this.props.to_location.loadingFilterSites}
                      allowClear={true}
                      notFoundContent={
                        this.state.filter.id_location ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                          <Empty
                            description={<IntlMessages id="noLocation" />}
                          />
                        )
                      }
                    >
                      {customers.map((data, key) => (
                        <Option key={key} value={data.id_customer}>
                          {`${data.customer_code} (${data.customer_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="year" />
                        </label>
                        <br />
                        <DatePicker
                          format="YYYY"
                          allowClear={false}
                          style={{ width: "95%" }}
                          value={this.state.filter.year}
                          mode="year"
                          onOpenChange={status =>
                            status ? this.setState({ open: status }) : null
                          }
                          onPanelChange={value => {
                            this.handleFilterChange("year", value);
                            this.setState({ open: false });
                          }}
                          open={this.state.open}
                        />
                      </Col>

                      <Col
                        span={12}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clickFilter" />} />
            ) : yearly_sales_reports.length === 0 &&
              !this.props.yearlySalesReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="yearlySalesReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    yearlySalesReport: state.yearlySalesReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(YearlySalesReport);
